/*
SCSS Build File


General Layout
*/
@import url("https://fonts.googleapis.com/css2?family=Jost:wght@300;400;600;900&display=swap");
html, body {
  height: 100%;
  min-height: 100%;
  background: #0f0d1d;
  margin: 0;
  padding: 0;
  font-family: "Jost", sans-serif;
  position: relative;
  z-index: -1;
}

.sr-only {
  width: 0;
  height: 0;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  display: block;
}

/*

Header Styles

*/
header {
  position: absolute;
  display: flex;
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
  height: 115px;
  box-sizing: border-box;
  top: 0;
  width: 100%;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  border-right: 1px solid rgba(255, 255, 255, 0.25);
  margin: 0;
  text-align: center;
}
@media all and (min-width: 1280px) {
  .logo {
    max-width: 315px;
    min-width: 315px;
  }
}
.logo img {
  max-width: 80%;
  max-height: 70%;
}

body.nav-open {
  overflow: hidden;
}

nav.primary {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
nav.primary ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: flex-start;
}
@media all and (min-width: 1280px) {
  nav.primary ul {
    flex-direction: row;
    position: static;
    width: auto;
  }
}
nav.primary ul.navigation {
  position: fixed;
  padding: 0;
  width: 100vw;
  height: calc(100vh - 150px);
  background: rgba(0, 0, 0, 0.9);
  left: 100%;
  top: 115px;
  z-index: 10;
  flex-direction: column;
  transition: 0.33s all ease-in-out;
}
@media all and (min-width: 1280px) {
  nav.primary ul.navigation {
    position: static;
    display: flex;
    align-items: center;
    width: 100%;
    height: auto;
    background: transparent;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
  }
}
.nav-open nav.primary ul.navigation {
  left: 0;
}
nav.primary ul.navigation li {
  padding: 0;
  line-height: 100px;
  min-height: 100px;
  margin: 0;
  position: relative;
}
@media all and (min-width: 1280px) {
  nav.primary ul.navigation li {
    line-height: 1rem;
    height: auto;
    margin-right: 40px;
    min-height: 0;
  }
  nav.primary ul.navigation li:hover .subnav {
    opacity: 1;
    pointer-events: all;
    top: 100%;
  }
}
nav.primary ul.navigation .nav-item {
  padding: 0 40px;
  width: 100%;
  display: block;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
}
@media all and (min-width: 1280px) {
  nav.primary ul.navigation .nav-item {
    width: auto;
    height: auto;
    border: 0;
    padding: 0;
    display: inline-block;
  }
}
nav.primary ul.navigation .nav-item::after {
  position: absolute;
  height: 2px;
  background: #fff;
  width: 0;
  bottom: -5px;
  left: 0;
  border-radius: 2px;
  transition: all 0.33s ease-in-out;
}
@media all and (min-width: 1280px) {
  nav.primary ul.navigation .nav-item::after {
    content: "";
  }
}
nav.primary ul.navigation .nav-item:hover {
  cursor: pointer;
}
nav.primary ul.navigation .nav-item:hover::after {
  width: 100%;
}
nav.primary ul.navigation .nav-item.active::after {
  width: 15px;
}
nav.primary ul.navigation .nav-item.active:hover::after {
  width: 100%;
}
nav.primary ul.social-media {
  padding-right: 25px;
  border-right: 1px solid rgba(255, 255, 255, 0.25);
  margin-left: auto;
}
nav.primary ul.social-media li {
  margin-right: 0;
}
nav.primary ul.social-media a {
  width: 26px;
  height: 100%;
  display: block;
  background: transparent no-repeat center;
  background-size: 14px;
}
nav.primary ul.social-media a.facebook {
  background-image: url("/assets/dist/images/facebook.png");
}
nav.primary ul.social-media a.youtube {
  background-image: url("/assets/dist/images/youtube-brands.svg");
}
nav.primary li {
  color: #fff;
  font-size: 18px;
  line-height: 115px;
  margin-right: 30px;
}
nav.primary a {
  color: #fff;
  text-decoration: none;
  position: relative;
}
nav.primary ul.subnav {
  display: block;
  border-bottom: 2px solid rgba(255, 255, 255, 0.25);
  margin-top: -3px;
  position: relative;
}
@media all and (min-width: 1280px) {
  nav.primary ul.subnav {
    position: absolute;
    left: 0;
    opacity: 0;
    pointer-events: none;
    top: calc(100% - 20px);
    transition: all 0.33s ease-out;
    padding: 20px;
    border-radius: 0 0 10px 10px;
    border: 0;
    background: rgba(0, 0, 0, 0.75);
  }
}
nav.primary ul.subnav li {
  height: auto;
  min-height: 0;
  line-height: 2.75rem;
  background: #000;
  border: 0;
}
nav.primary ul.subnav li a {
  border: 0;
  padding: 0 0 0 60px;
  display: block;
}
@media all and (min-width: 1280px) {
  nav.primary ul.subnav li a {
    padding: 10px 0;
    white-space: nowrap;
  }
}

/* mobile menu toggle button */
.menu-toggle {
  position: relative;
  display: inline-block;
  width: 90px;
  height: 116px;
  padding: 40px 25px;
  cursor: pointer;
  box-sizing: border-box;
  order: 4;
}
@media all and (min-width: 1280px) {
  .menu-toggle {
    display: none;
  }
}
.menu-toggle span {
  margin: 0 auto;
  position: relative;
  top: 12px;
  transition-duration: 0s;
  transition-delay: 0.2s;
  transition: background-color 0.3s;
  width: 40px;
  height: 6px;
  background-color: #fff;
  display: block;
  opacity: 1;
}
.menu-toggle span::before, .menu-toggle span::after {
  position: absolute;
  content: "";
  width: 40px;
  height: 6px;
  background-color: #fff;
  display: block;
  opacity: 1;
}
.menu-toggle span::before {
  margin-top: -12px;
  transition-property: margin, transform;
  transition-duration: 0.2s;
  transition-delay: 0.2s, 0;
}
.menu-toggle span::after {
  margin-top: 12px;
  transition-property: margin, transform;
  transition-duration: 0.2s;
  transition-delay: 0.2s, 0;
}
.nav-open .menu-toggle span {
  background-color: rgba(0, 0, 0, 0);
  transition: 0.3s background-color;
}
.nav-open .menu-toggle span::before {
  margin-top: 0;
  transform: rotate(45deg);
  transition-delay: 0, 0.2s;
}
.nav-open .menu-toggle span::after {
  margin-top: 0;
  transform: rotate(-45deg);
  transition-delay: 0, 0.2s;
}

